.pokemon {
  text-align: center;

  .radio-container {
    justify-content: space-around;
    align-items: center;
    margin-bottom: 20px;
    padding: 3rem;
    background: $color-3;
    border-radius: 20px;
    padding: 5px;
    color: $white;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.452);

    input {
      transform: translateY(1px);
    }

    label {
      margin-left: 10px;
      cursor: pointer;
    }
  }

  input[type="text"] {
    padding: 4px 10px; /* Correction de la syntaxe */
    border-radius: 40px;
    font-size: 1rem;
    border: 3px solid $color-1;
    outline: none;
    transition: 0.4s;
    text-align: center;
    width: 350px;
    margin: 1rem;

    &:focus {
      background: $color-1;
      border-radius: 4px;
      color: $white;
    }
  }

  button, select {
    background: none;
    border: 2px solid $color-1;
    padding: 4px 10px;
    border-radius: 10px;
    cursor: pointer;
    transition: 0.2s;
    background-color: white;
    color: rgb(125, 125, 124);
    margin: 1rem;

    &:hover {
      background: $color-1;
    }
  }

  ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  .pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
  }

  .pagination-button {
    margin: 0 5px;
    padding: 10px 20px;
    border: none;
    background-color: #f0f0f0;
    cursor: pointer;
    transition: background-color 0.3s;
  }

  .pagination-button:hover {
    background-color: #e0e0e0;
  }

  .pagination-button.active {
    background-color: #007bff;
    color: white;
  }

  .pagination-button.active:hover {
    background-color: #0056b3;
  }

  button:disabled {
    cursor: not-allowed;
    opacity: 0.5;
  }

  @media (max-width: 768px) {
    .pagination-button.page-number {
        display: none;
    }

    .pagination-button.prev-next {
        display: inline-block;
    }

  }
}


