.logo {
  @include verticalCenter;
  margin: 5px auto;
  border-radius: 20px;
  padding: 5px 10px;
  height: 200px;
  width: 100%;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.307);


  h3 {
    margin-left: 10px;
    color: $color-1;
  }

}


@media screen and (max-width: 768px) {
  .logo {
      width: 100%;
      height: auto;
  }
}

