.pokemon-detail {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    display: flex;
    flex-direction: column;
}
.pokemon-details p {
    margin: 5px;
    font-size: 18px;
}
.pokemon-details span {
    text-transform: uppercase;
    color: white;
}

.pokemon-detail h1 {
    font-size: 30px;
    margin-bottom: 10px;
    text-transform: uppercase;
}

.pokemon-info {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}
.type-images {
    display: flex;
    justify-content: center;
    margin-top: 10px;
}

.type-images img {
    width: 50px;
    height: 50px;
    margin: 10 5px;
}
.pokemon-image {
    flex: 0 0 40%;
    margin-right: 20px;
    filter: drop-shadow(33px -9px 10px #d8caca);
}

.pokemon-image img {
    max-width: 100%;
    height: auto;
    display: block;
    margin-bottom: 10px;
}

.pokemon-details {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 50px;
    padding: 20px;
    background: rgba(0, 0, 0, 0.399);
    border-radius: 27px;
    box-shadow: rgba(17, 17, 26, 0.1) 0px 4px 16px, rgba(17, 17, 26, 0.05) 0px 8px 32px;
}

.evolutions {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; 
}

.evolutions p {
    font-weight: bold;
    margin-bottom: 10px;
}

.evolutions ul {
    list-style-type: none;
    padding: 0;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.evolutions li {
    margin-right: 20px;
    display: flex;
    align-items: center;
}

.evolutions li img {
    max-width: 100px;
    height: auto;
    margin-right: 10px;
    filter: drop-shadow(33px -9px 10px #d8caca);
}
.evolutions li div {
    margin: 25px;
}
@media (max-width: 768px) {
    .pokemon-info {
        flex-direction: column;
        align-items: center;
    }

    .pokemon-image {
        margin-right: 0;
        margin-bottom: 20px;
    }
}
